
@import '_variables';

*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

.nav-list {
    background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
    height: 10vh;
    display: flex;
    margin-top: 3%;
    justify-content: space-around;
    align-items: center;
    margin-left: auto;
    li {
        list-style-type: none;
    }
}

.nav-links {
    text-decoration: none;
    color: black;
    font-size: 18px;
}

.home {
    height: 73vh;
    position: absolute;
    width: 100%;
}

.home-content {
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    img { 
        height: 30%; 
    }
    p {
        font-size: 1.8em;
        font-weight: 600;
        margin: 30px 0px;
        width: 50%;
    }
}
.home {
    margin-top: 50px;
    background-image: linear-gradient(to top, #a8edea 0%, #fed6e3 100%);
}
